<template>
  <div>
      <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
        >
     <!-- <van-cell v-for="(item,index) in list" :key="index" :title="item.positionName" /> -->
          <ul>
            <li v-for="(item,index) in list" :key="index">
                <img :src="'https://www.lgstatic.com/'+item.companyLogo" width="100" />
            </li>
          </ul>
      </van-list>
  </div>
</template>

<script>
import axios from 'axios';
export default {
   data(){
     return {
        list:[],
        loading: false,//不是正在加载，可以发请求
        finished: false, //表示是否加载完成了。
        pageNo:1
     }
   },
   methods:{
     onLoad(){ //每次onLoad取新数据
       this.getData();
     },
     getData(){  //取数据
        axios.get("/lg/listmore.json",{
          params:{
            pageNo:this.pageNo,
            pageSize:10
          }
        }).then((res)=>{
            console.log(res)
            //新数据和旧的数据联合到一块
            this.list = this.list.concat(res.data.content.data.page.result);
            this.pageNo++; //加载数据后，把页码变为下一页
            this.loading=false; //发请求后，要把loading变为false才能请求下一次的数据
            if(this.list.length>100){
              this.finished=true; //大于100条数据 ，停止加载
            }
        })
     }

   }
}
</script>

