import axios from './request';
//这个axios是设置过请求超时事件和baseURL的axios
//我们把改造过的axios叫做axios的二测封装
//获取电影列表
export const getFilm=(type)=>{
    // /mz 是替代  https://m.maizuo.com
    //?cityId=110100...... 放到 params里面 
    // return axios.get("/gateway",{
    //     params:{
    //         cityId:110100,
    //         pageNum:1,
    //         pageSize:10,
    //         type:type,
    //         k:Date.now()  //事件戳，防止get缓存
    //     },
    //     headers:{          
    //         "X-Host": "mall.film-ticket.film.list"
    //     }
    // })
    return axios.get(`/gateway`,{
        params:{
            cityId:110100,
            pageNum:1,
            pageSize:10,
            type,
            k:Date.now()
        },
        headers:{          
                   "X-Host": "mall.film-ticket.film.list"
             }
    })
}
//获取咨询页面的内容
export const getInfo=()=>{
  return   axios.get("/gateway?actId=ElzMZU125260",{
        headers:{
            "X-Host":"mall.act.static-page.info"
        }
    })
}
//获取城市列表
export const getCities=()=>{
    return axios.get("/gateway?k=7719936",{
        headers:{
            "X-Host":"mall.film-ticket.city.list"
        }
    })
}
//根据ID获取影片的介绍内容
export const getDetail=(id)=>{
    return axios.get(`https://m.maizuo.com/gateway?filmId=${id}&k=163091`,{
        headers:{
            "X-Host": "mall.film-ticket.film.info"
        }
    })
}