<template>
   <div>
       <div id="box" v-show="false" style="width:100%;height:300px;"></div>
        <van-index-bar>
          <div v-for="item in letters" :key="item">
            <van-index-anchor :index="item" />
            <van-cell @click="saveCityNameId(subItem.name,subItem.cityId)" :title="subItem.name" v-for="subItem in cityObj[item]"
             :key="subItem.cityId" />
          </div>
        </van-index-bar>
   </div>
</template>

<script>
import {getLocation} from '@/util/location'
import {getCities} from '@/api'
import {mapMutations} from 'vuex';
export default {
        data(){
            return {
                sortCities:[], //排好序的城市列表
                cityObj:{},  // {'A':['..','..'],'B':['...','...']}
                letters:[]
            }
        },
        created(){
                getCities().then((res)=>{
                    //排序城市
                 this.sortCities =  res.data.cities.sort((a,b)=>a.pinyin.localeCompare(b.pinyin));
                  res.data.cities.forEach((item)=>{
                      //取城市拼音的第一字字母，变为大写
                      var firstLetter = item.pinyin[0].toUpperCase();
                      //每个大写的字母都在cityObj对象里建立一个数组
                      if(!this.cityObj[firstLetter]){
                          this.cityObj[firstLetter]=[];
                      }
                      //把每个城市按照字母放到数组里
                      this.cityObj[firstLetter].push({
                          cityId:item.cityId,
                          name:item.name,
                          pinyin:item.pinyin,
                          isHot:item.isHot
                      })
                  })
                  //所有城市拼音的大写首字母形成一个数组
                   this.letters = Object.keys(this.cityObj);

                   //定位
                    getLocation("box",(cityName)=>{
                        cityName = cityName.replace("市","");
                        if(cityName){
                            //根据名字得到cityId
                            var id = this.sortCities.filter((item)=>item.name===cityName)[0].cityId;
                            this.saveCityNameId(cityName,id)
                            //如果有定位 就跳到首页
                            //this.$router.push("/")
                        }
                    })
                   
               })
        },
        mounted(){
            // getLocation("box",(data)=>{
            //     console.log(data.replace("市",""))
            // })
        },
        methods:{
            ...mapMutations(["setName","setId"]),
            //把城市的名字和id 保存到本地存储和vuex的store
            saveCityNameId(name,id){
               localStorage.setItem("cityName",name)
               localStorage.setItem("cityId",id)
               this.setName(name);
               this.setId(id);
            }
        }
}
</script>

