import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
     cityId:localStorage.getItem("cityId")||"110100",
     cityName:localStorage.getItem("cityName")||"北京"
  },
  mutations: {
      setName(state,name){
        state.cityName = name;
      },
      setId(state,id){
        state.cityId = id;
      }
  },
  actions: {
  },
  modules: {
  }
})
